export const getUnbreakableCoinBonusFromDescription = (descriptions:string, countCoin:number) : boolean[] => {
  const unbreakableCoinBuff = new Array(countCoin).fill(false); //  

  const regex = /@(\d+)@\s*#unbreakableCoin#/g; //      
  const matches = Array.from(descriptions.matchAll(regex)); //    

  for (const match of matches) {
    const index = parseInt(match[1], 10); //   @x@
    
    // ,   
    if (index >= 0 && index-1 < countCoin) {
      unbreakableCoinBuff[index-1] = true; //   true   
    }
  }

  return unbreakableCoinBuff;
}